.App {
  text-align: center;
}
*{
      margin: 0;
      padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
html, body, #root, .App {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.container{
  width: 100%;
  height: 100%;
 
}
.test {
  display: flex;
  flex-direction: column; /* 요소들을 수직으로 배치합니다. */
  align-items: center; /* 요소들을 수직 중앙에 정렬합니다. */
}

.test button, .test .react-calendar {
  margin-top: 10px; /* 버튼과 캘린더 간의 간격을 조정합니다. */
}

.react-calendar {
  width: 80vw !important;
  
  height: 80vh;
  background: white;
  border: 1px solid #a0a096;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1em;
  font-weight: bold;
  line-height: 1.125em;
}
.react-calendar__viewContainer {
  width: 80vw;
  height: 75vh;
}

.react-calendar__tile {
  /* 캘린더의 각 날짜 타일의 스타일을 설정합니다. */
  /* 예시: */
  height: 12vh;
  font-size: 1rem !important;
  background-color: black ;
  border-radius: 15%;
  border: 1px solid #ddd !important;
  cursor: pointer;
}

.react-calendar__month-view__weekdays__weekday { /*요일적힌칸 */
  font-size: 1.2rem;
  border-bottom: 3px solid blanchedalmond;
  border-right: 3px solid blanchedalmond;

}

.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus { /*호버 할때*/
  background-color: #e6e6e6 !important;
}
.react-calendar__tile--now {
  background: #ffff76;
}
.react-calendar__tile--active { /*선택햇을시 */
  background: #006edc;
  opacity: 0.5;
  color: white;
}
.react-calendar__tile--active:enabled:hover,
.react-calendar__tile--active:enabled:focus {
  background: gray !important;
}

.lobbyColorContainer{
  margin-top: 10px;
  display: flex;
  width: 100%;
  justify-content: flex-end;
  margin-right: 10px;
}
.lobbyColor{
  height: 30px;
  width: 30px;
  border-radius: 50%;
  margin-left: 15px;
}
.lobbyBackground {
  position: relative;
  overflow-y: scroll;
  width: 100%; 
  height: 95vh;
  border: 1px solid #ff0000;

  
}

.lobbyContainer {
 
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  justify-content: center;
  


}
.lobbyList{
  height: 350px;
  width: 250px;
  font-weight: bold;
  margin: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 2px;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 1);

}

.lobbyImg{
  margin-top: 5px;
}
.lobbyImg img{
  object-fit: cover;
  width: 200px;
  height: 200px;
  
}
.lobbyTitle{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  border-bottom: 3px solid black;
}

.titleHeader{
  border: 1px solid black;
  background-color: #dcdcdc;
  font-size: large;
}

.mainBody{
  position: relative;
  margin: 0 auto;
  height: 90vh;
  width: 90vw;
}



/* Tab 컴포넌트를 감싸는 컨테이너 */
.tabContainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

/* 탭 버튼들을 담는 div */
.tabButton {
  display: flex;
  justify-content: space-evenly;
}


.tabMain{
  width: 70%;
  height: 100%;
}
.tabSide{
  border: 1px solid black;
  width: 30%;
  overflow-y: scroll;
  height: 84.5vh;

}
.tabSideTitle{
  border-bottom: 3px solid black;
  width: 100%;
  

}
.sideList{
  height: 10%;
  max-width: 100%; 
  border: 1px solid black;
  border-radius: 10px;
  display: flex;
  margin-top: 5px;
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 5px;
  background-color: white;
  
}
.sideListTitle{
 display: flex;
 width: 100%;
 border-right: 1px solid black;
 justify-content: center;
  align-items: center;
}
.sideListContent{
  
  font-size: large;
  font-weight: bold;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  
}
.sideListButton{
  height: 100%;
  width: 15%;
  
}
.sideListButton button{
  border-radius: 10%;
  height: 100%;
  width: 100%;
  cursor: pointer;
  background-color: #f0f0f0;
  border: 1px solid black;
  

}
.sideListButton:hover button{
  font-size: large; 
  background-color: #e6b36d;
}


/* 각 탭 버튼 스타일 */
.tabButton button {
  width: 15%;
  height:  10vh;
  font-size: 30px;
  border: 2px solid black;
  background-color: #ffffff;

  color: #333333;
  padding: 8px 16px;
  cursor: pointer;
  margin-right: 8px;
  border-radius: 4px;

}

/* 활성화된 탭 버튼 스타일 */
.tabButton button.active {
  color: #ffffff;
  border-color: #007bff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); 
}

 .tabContentWrapper{
   height: 100%;
   border: 1px solid black;

 }
.tabContent {
  margin-top: 16px;
  padding: 16px;
}

.weightContainer{
  display: grid;
  height: 70vh;
  overflow-y: scroll;
  gap: 20px; /* 열과 열 사이의 간격 */
}
.weightImg {
  width: 15vw;
  height: 20vh;
  object-fit: cover;
 
}
.weightImg img {
  width: 100%;
  height: 100%;
}
.weightItem{
  height: 100%;
  display: grid;
   grid-template-columns: auto 1fr;
  border: 1px solid black;
}

.weightContent{
  border: 1px solid black;
  display: flex;
  flex-direction: column;

}
.weightTitle{
  border: 1px solid black;
}
.weightMain{
  font-size: large;
}
.weightMain a {
  text-decoration: none;
  color: black;
  font-weight: bold;
}
.weightRep{
  width: 5%;
}
.weightList{
  height: 100%;
}
.lobbyBtnContainer{
  text-align: right;
}
.recordButton,
.gobackButton{
   display: inline-block;
   margin-right: 20px;
    cursor: pointer;
    padding: 1em 2em;
    border-radius: 0;
    color: #b2876f;
    margin-top:2rem;
    font-weight: bold;
    font-size: 0.8rem;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-decoration: none;
  background: linear-gradient(to right, rgba(#b2876f, 0) 25%, rgba(#b2876f, .8) 75%);
  background-position: 1% 50%;
  background-size: 400% 300%;
  border: 1px solid #b2876f;
  transition: all ease 0.3s;
   box-shadow: 0 2px 6px rgba(0, 0, 0, 0.6);
  &:hover{
    scale: 1.2;
    color: black;
      color: black;
      background-position: 99% 50%;
  }
}





@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
